$colors: (
  primary:    		#6777ef,
  primary2:    		#6777ef,
  secondary:      #cdd3d8,
  success:        #69e7b8,
  success2:        #16c655,
  info:           #286cf5,
  info2:          #2fe3f7,
  warning:        #ffa426,
  warning2:        #ffe626,
  danger:         #d62a27,
  danger2:         #9b3cb7,
  light:          #e3eaef,
  grey:  		      #868e96,
  whitesmoke:     #f7f9f9,
  dark:       		#191d21,
  muted:          #98a6ad,
  body:           #f1f1f166,
  font:           #474c51,
  fontdark:       #34395e,
  white:          #ffffff
);

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px
) !default;

@function color($key) {
	@return map-get($colors, $key);
}
@function color_lighten($key, $amount) {
	@return lighten(map-get($colors, $key), $amount);
}

@function color_darken($key, $amount) {
	@return darken(map-get($colors, $key), $amount);
}

@function to_opacity($color, $opacity) {
  @return rgba(color($color), $opacity);
}